@import url(https://fonts.googleapis.com/css2?family=Yantramanav:wght@300;400;500&display=swap);
*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.App {
    font-family: 'Yantramanav', sans-serif;
    display: flex;
    align-items: center;
    flex-direction: column;
}

#mainTitle {
    text-align: center;
    margin: 25px;
    font-size: 3rem;
    font-weight: 500;
    color: #000;
}

#posts {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.comments {
    display: flex;
    flex-direction: column;
    padding: 10px 15px 15px 15px;
}

#comment {
    display: flex;
    flex-direction: column;
    padding: 5px 0;
    margin: 10px 0;
    border-bottom: 4px solid #90a4ae;
    border-radius: 6px;
}

#comSectionTitle {
    padding: 5px;
    font-size: 1.7rem;
    font-weight: 500;
}

#comName {
    font-weight: 500;
    font-size: 1.3rem;
    background-color: #90a4ae;
    padding: 4px 0 4px 5px;
    border-radius: 6px 6px 0 0;
}

#comEmail {
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 5px;
    padding-left: 5px;
}

#comBody {
    font-size: 1.1rem;
    padding-left: 5px;
}
.singlePost {
    display: flex;
    flex-direction: column;
    width: 720px;
    margin: 30px 0;
    background-color: #b0bec5;
    color: #000;
    border-radius: 7px;
    box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    text-decoration: none;
}

#title {
    text-align: center;
    font-size: 1.95rem;
    font-weight: 500;
    padding: 25px;
}

#body {
    text-align: center;
    font-size: 1.1rem;
    background-color: #cfd8dc;
    padding: 25px;
}

#userName {
    padding: 15px 15px 0 15px;
    display: flex;
    justify-content: flex-end;
    font-size: 1.1rem;
}

.toolbar {
    display: flex;
    width: 70vw;
    padding: 30px 25px;
    justify-content: space-between;
    align-items: center;
    margin: 40px auto 40px auto;
    font-size: 1.1rem;
    background-color: #cfd8dc;
    border-radius: 10px;
    box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.2);
}

label {
    margin-right: 5px;
}

input, option, select {
    font-family: 'Yantramanav', sans-serif !important;
    font-size: 16px;
    cursor: pointer;
}

#search {
    padding: 4px;
    height: 35px;
    cursor: text;
}

select {
    padding: 2px;
}

#searchBtn {
    height: 35px;
    width: 45px;
    cursor: pointer;
    font-size: 16px;
    outline: none;
}

#filterDiv, #searchDiv {
    display: flex;
    flex-direction: row;
}

.singlePagePost {
    display: flex;
    flex-direction: column;
    width: 55%;
    margin: 20px 0;
    background-color: #b0bec5;
    color: #000;
    border-radius: 7px;
    box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.2);
    text-decoration: none;
    cursor: default;
}

#titleSP {
    text-align: center;
    font-size: 1.85rem;
    font-weight: 500;
    padding: 25px;
}

#bodySP {
    text-align: center;
    font-size: 1.1rem;
    background-color: #cfd8dc;
    padding: 25px;
}

#userNameSP {
    padding: 15px 15px 0 15px;
    display: flex;
    justify-content: flex-end;
    font-size: 1.1rem;
}
